:root {
  --brand-color: #e86f54;
  --brand-color-hover: #e25b41;
  --brand-color-focus: #fa6f54;
  --black: #000;
  --white: #fff;
  --error-state: #e74a3b;
  --correct-state: #3cbc81;
}

body {
  color: var(--black);
  background-color: var(--white);
  font-family: Montserrat, sans-serif;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

ul, ol {
  margin: 0;
  padding-left: 0;
  list-style: none;
}

.link {
  text-decoration: none;
}

.store-header-div {
  width: 100%;
}

.store-header {
  align-items: center;
  margin-left: 16px;
  display: flex;
  position: relative;
}

.store-header:after {
  content: " ";
  text-shadow: none;
  background-color: var(--black);
  flex-grow: 1;
  height: 1px;
  margin-left: 14px;
  margin-right: 16px;
}

.store-main-div {
  display: flex;
}

.store-list {
  flex-wrap: wrap;
  justify-content: center;
  gap: 16px;
  margin: 32px 16px;
  display: flex;
}

.store-list-element {
  width: calc(50% - 8px);
  position: relative;
}

.store-offers {
  flex-direction: column;
  display: flex;
}

.store-offers-item {
  margin-top: 12px;
  margin-bottom: 0;
  font-size: 16px;
  font-weight: 500;
  line-height: 19.5px;
}

.store-offers-item-description {
  opacity: .6;
  margin-top: 6px;
  font-size: 12px;
  font-weight: 600;
  line-height: 14.63px;
}

.store-button-div {
  justify-content: center;
  display: flex;
}

.store-button {
  background-color: var(--brand-color);
  color: var(--white);
  cursor: pointer;
  border: none;
  border-radius: 38px;
  width: 208px;
  height: 44px;
  font-size: 20px;
  line-height: 24px;
}

.store-button:hover {
  background-color: var(--brand-color-hover);
}

.store-button:focus {
  background-color: var(--brand-color-focus);
}

@media screen and (width >= 768px) and (width <= 1158px) {
  .store-header {
    font-size: 44px;
  }

  .store-header:after {
    margin-left: 16px;
    margin-right: 24px;
  }

  .store-main-div {
    max-width: 768px;
  }

  .store-list {
    width: 100%;
  }

  .store-list-element {
    width: 344px;
    height: 491px;
  }

  .store-offers-item:after {
    content: "";
    background-color: var(--black);
    flex-grow: 1;
    height: 1px;
    margin-left: 8px;
  }

  .store-button {
    width: 280px;
    height: 64px;
    font-size: 28px;
  }

  .store-offers-item {
    align-items: center;
    margin-top: 16px;
    font-size: 32px;
    display: flex;
    position: relative;
  }

  .store-offers-item-description {
    margin-top: 10px;
    font-size: 24px;
  }

  picture {
    min-width: calc(50% - 16px);
  }
}

@media screen and (width >= 1158px) {
  .store-header:after {
    margin-left: 16px;
    margin-right: 24px;
  }

  .store-list {
    gap: 32px;
    margin: 60px 24px;
  }

  .store-list-element {
    width: 600px;
    height: 640px;
  }

  .store-offers-item {
    align-items: center;
    margin-top: 16px;
    font-size: 36px;
    font-weight: 500;
    display: flex;
    position: relative;
  }

  .store-offers-item-description {
    margin-top: 15px;
    font-size: 28px;
  }

  .store-offers-item:after {
    content: "";
    background-color: var(--black);
    flex-grow: 1;
    height: 1px;
    margin-left: 8px;
  }

  .store-button {
    width: 280px;
    height: 64px;
    font-size: 28px;
  }

  .store-header {
    font-size: 44px;
  }
}

.modal-contact {
  background-color: var(--white);
  width: 100%;
  max-width: 272px;
  height: 357px;
  padding: 28px 16px;
  transition: transform .25s cubic-bezier(.4, 0, .2, 1);
  position: fixed;
  top: 55%;
  left: 50%;
  transform: translate(-50%, -50%)scale(1);
}

.backdrop {
  background-color: #0000004d;
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0;
}

.is-hidden {
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
}

.close-button-modal {
  fill: var(--black);
  cursor: pointer;
  background-color: #0000;
  border: none;
  justify-content: center;
  align-items: center;
  width: 18px;
  height: 18px;
  padding: 0;
  display: flex;
  position: absolute;
  top: 16px;
  right: 10px;
}

#icon-x-mobile path {
  stroke: #000;
}

.title-contact {
  color: var(--black);
  margin: 0 0 14px;
  font-family: Segoe UI, Tahoma, Geneva, Verdana, sans-serif;
  font-size: 20px;
  font-weight: 600;
}

.legend {
  color: var(--grey);
  margin-bottom: 18px;
  font-size: 12px;
  font-weight: 500;
}

.form {
  flex-direction: column;
  display: flex;
}

.label {
  color: var(--grey);
  margin-bottom: 8px;
  font-size: 10px;
  font-weight: 500;
}

.input {
  border: 1px solid var(--milk);
  color: #0006;
  border-radius: 4px;
  outline: #0000;
  margin-bottom: 14px;
  padding: 10px 12px;
  font-size: 12px;
  font-weight: 500;
}

.input:hover, .input:focus {
  border: 1px solid #111111b3;
}

.input:not(:placeholder-shown):required:invalid {
  border: 1px solid var(--error-state);
}

.input:not(:placeholder-shown):required:valid {
  border: 1px solid var(--correct-state);
}

.button-send {
  color: var(--white);
  cursor: pointer;
  background-color: #e86f5466;
  border: none;
  border-radius: 8px;
  justify-content: center;
  align-items: center;
  width: 100%;
  max-width: 240px;
  height: 37px;
  margin-top: 10px;
  padding: 10px 0;
  font-size: 14px;
  font-weight: 600;
  display: flex;
}

.button-send:active {
  background-color: var(--brand-color);
}

@media screen and (width >= 768px) {
  .modal-contact {
    max-width: 480px;
    height: 511px;
    padding: 48px 32px;
  }

  .title-contact {
    margin-bottom: 24px;
    font-size: 32px;
  }

  .legend {
    margin-bottom: 32px;
    font-size: 16px;
  }

  .label {
    font-size: 14px;
  }

  .input {
    margin-bottom: 24px;
    padding: 16px;
    font-size: 14px;
  }

  .button-send {
    max-width: 416px;
    height: 50px;
    padding: 14px;
    font-size: 18px;
  }

  .icon-x {
    width: 24px;
    height: 24px;
    padding: 0;
  }

  .close-button-modal {
    width: 24px;
    height: 24px;
    top: 24px;
    right: 18px;
  }
}

